import { createSlice } from '@reduxjs/toolkit';
import { SLICE_NAMES } from '../Constants';
import IArticlesState from '../interfaces/IArticlesState';
import { getArticles, getFilteredArticles } from '../reducers/ArticlesReducer';
import { RootState } from './Store';

const initialState: IArticlesState = {
  value: [],
  status: 'idle',
};

export const articlesSlice = createSlice({
  name: SLICE_NAMES.ARTICLES,
  initialState,
  reducers: {},
  extraReducers: (builder) => {
    builder
      .addCase(getArticles.pending, (state) => {
        state.status = 'pending';
      })
      .addCase(getArticles.fulfilled, (state, action) => {
        state.status = 'fulfilled';
        state.value = action.payload;
      })
      .addCase(getFilteredArticles.pending, (state) => {
        state.status = 'pending';
      })
      .addCase(getFilteredArticles.fulfilled, (state, action) => {
        state.status = 'fulfilled';
        state.value = action.payload;
      });
  },
});

export const selectArticlesState = (state: RootState) => state.articles;
export const selectArticles = (state: RootState) => state.articles.value;
export const selectArticlesStatus = (state: RootState) => state.articles.status;

export default articlesSlice.reducer;
