import { createAsyncThunk } from '@reduxjs/toolkit';
import { API } from '../Constants';
import IArticle from '../interfaces/IArticle';

export const getArticles = createAsyncThunk(
  'getArticles',
  async (): Promise<IArticle[]> => {
    try {
      const response = await fetch(API.GET_ARICLES());
      const articlesWithRedundantData: [] = await response.json();

      // FIXME: name for the constant
      const articles: IArticle[] = articlesWithRedundantData.map(
        ({ id, title, summary, publishedAt, imageUrl }) => {
          if (!publishedAt) {
            throw new Error('No date');
          }

          return {
            id,
            title,
            body: summary,
            coverUrl: imageUrl,
            publishedAt,
          };
        }
      );

      return articles;
    } catch (error) {
      console.log(error);

      return await Promise.reject(error);
    }
  }
);

export const getFilteredArticles = createAsyncThunk(
  'getFilteredArticlesByTitle',
  async (filter: string): Promise<IArticle[]> => {
    try {
      if (filter.length === 0) {
        throw new Error('Specify the filtering string!');
      }

      const response = await fetch(API.GET_FILTERED_ARTICLES_BY_TITLE(filter));
      const articlesWithRedundantData: [] = await response.json();

      // FIXME: name for the constant
      const articles: IArticle[] = articlesWithRedundantData.map(
        ({ id, title, summary, publishedAt, imageUrl }) => {
          if (!publishedAt) {
            throw new Error('No date');
          }

          return {
            id,
            title,
            body: summary,
            coverUrl: imageUrl,
            publishedAt,
          };
        }
      );

      return articles;
    } catch (error) {
      console.log(error);

      return await Promise.reject(error);
    }
  }
);
