import SearchIcon from '@mui/icons-material/Search';
import { Box, Container, Divider, TextField, Typography } from '@mui/material';
import Mark from 'mark.js';
import { useState } from 'react';
import { selectArticles } from '../../app/ArticlesSlice';
import { useAppDispatch, useAppSelector } from '../../app/Hooks';
import {
  getArticles,
  getFilteredArticles,
} from '../../reducers/ArticlesReducer';

const Header = () => {
  const [filter, setFilter] = useState('');

  const articles = useAppSelector(selectArticles);

  const dispatch = useAppDispatch();

  const searchArticles = (filter: string) => {
    const fetchAndHighlightArticles = async () => {
      setFilter(filter);

      if (filter.length === 0) {
        await dispatch(getArticles());

        return;
      }

      await dispatch(getFilteredArticles(filter));

      const titlesAndBodies = document.querySelectorAll('.textToHighlight');
      const instance = new Mark(titlesAndBodies);

      instance.mark(filter);
    };

    void fetchAndHighlightArticles();
  };

  return (
    <header>
      <Container maxWidth="lg">
        <Box sx={{ maxWidth: '1131px', mx: 'auto' }}>
          <Typography
            gutterBottom
            variant="body2"
            component="p"
            sx={{ fontWeight: 600, mt: 4, mb: 1 }}
          >
            Filter by keywords
          </Typography>

          <TextField
            sx={{
              p: 0.2,
              borderRadius: 1,
              mb: 2,
              boxShadow: 1,
              maxWidth: '44%',
              minWidth: 325,
            }}
            fullWidth
            size="small"
            variant="standard"
            InputProps={{
              startAdornment: (
                <SearchIcon sx={{ color: 'action.active', mx: 1, my: 0.5 }} />
              ),
              disableUnderline: true,
            }}
            onChange={(e) => searchArticles(e.target.value)}
            value={filter}
          />
          <Typography
            gutterBottom
            variant="body2"
            component="p"
            sx={{ fontWeight: 600 }}
          >
            Results: {articles.length}
          </Typography>
          <Divider sx={{ my: 0.5 }} />
        </Box>
      </Container>
    </header>
  );
};

export default Header;
