import KeyboardBackspaceOutlinedIcon from '@mui/icons-material/KeyboardBackspaceOutlined';
import {
  Button,
  Card,
  CardActions,
  CardContent,
  CardMedia,
  Container,
  Typography,
} from '@mui/material';
import { useLocation, useNavigate } from 'react-router-dom';
import { ROUTES } from '../Constants';

const ViewArticle = () => {
  const navigate = useNavigate();

  const { state } = useLocation();

  const { title, body, coverUrl } = state;

  const backBtnClick = () => {
    navigate(ROUTES.HOME);
  };

  return (
    <Container maxWidth="lg" disableGutters>
      <Card
        sx={{
          borderRadius: 0,
          boxShadow: 0,
          position: 'relative',
        }}
      >
        <CardMedia
          sx={{
            height: 180,
            width: '100%',
            position: 'absolute',
          }}
          image={coverUrl}
          title="Article cover"
        />
        <CardContent
          sx={{
            boxShadow: 3,
            px: 6,
            pb: 6,
            mx: 6,
            mt: '100px',
            bgcolor: 'white',
            position: 'relative',
          }}
        >
          <Typography
            sx={{
              textAlign: 'center',
              height: 70,
            }}
            gutterBottom
            variant="h5"
            component="h1"
          >
            {title}
          </Typography>
          <Typography variant="body2">{body}</Typography>
        </CardContent>
        <CardActions
          sx={{ mx: 6, p: 6, pt: 3, position: 'relative', zIndex: '4' }}
        >
          <Button
            size="small"
            sx={{
              textTransform: 'none',
              color: 'black',
              fontWeight: 800,
            }}
            startIcon={<KeyboardBackspaceOutlinedIcon />}
            onClick={() => backBtnClick()}
          >
            Back to homepage
          </Button>
        </CardActions>
      </Card>
    </Container>
  );
};

export default ViewArticle;
