import CalendarTodayOutlinedIcon from '@mui/icons-material/CalendarTodayOutlined';
import EastOutlinedIcon from '@mui/icons-material/EastOutlined';
import {
  Button,
  Card,
  CardActions,
  CardContent,
  CardMedia,
  Typography,
} from '@mui/material';
import { useNavigate } from 'react-router-dom';
import { DAY_NUMBERS_ENDINGS, ROUTES } from '../../Constants';
import IArticle from '../../interfaces/IArticle';

const ArticleCard = ({ article }: { article: IArticle }) => {
  const { title, body, publishedAt, coverUrl } = article;

  const publishedAtDate = new Date(publishedAt);

  const publishingDate = `${publishedAtDate.toLocaleString('default', {
    month: 'long',
  })} ${publishedAtDate.getDate()}${
    DAY_NUMBERS_ENDINGS[publishedAtDate.getDate()]
  }, ${publishedAtDate.getFullYear()}`;

  const navigate = useNavigate();

  const viewArticleBtnClick = () => {
    navigate(ROUTES.VIEW_ARTICLE, { state: article });
  };

  return (
    <Card sx={{ maxWidth: 345, m: 2 }}>
      <CardMedia sx={{ height: 140 }} image={coverUrl} title="Article cover" />
      <CardContent>
        <Typography
          gutterBottom
          variant="body2"
          component="div"
          color="text.secondary"
        >
          <CalendarTodayOutlinedIcon
            sx={{
              fontSize: 'inherit',
              verticalAlign: 'text-top',
              mr: 0.8,
            }}
          />
          {publishingDate}
        </Typography>
        <Typography
          sx={{ minHeight: 130 }}
          gutterBottom
          variant="h5"
          component="h1"
          className="textToHighlight"
        >
          {title}
        </Typography>
        <Typography variant="body2" className="textToHighlight">{`${body.slice(
          0,
          97
        )}...`}</Typography>
      </CardContent>
      <CardActions sx={{ p: 2, pt: 0 }}>
        <Button
          size="small"
          sx={{
            textTransform: 'none',
            color: 'black',
            fontWeight: 800,
          }}
          endIcon={<EastOutlinedIcon />}
          onClick={() => viewArticleBtnClick()}
        >
          Read more
        </Button>
      </CardActions>
    </Card>
  );
};

export default ArticleCard;
