const ARTICLES_QUANTITY_PER_REQUEST = 6;

const BASE_API_URL = 'https://api.spaceflightnewsapi.net/v3/articles';

const API = Object.freeze({
  GET_ARICLES: () => `${BASE_API_URL}?_limit=${ARTICLES_QUANTITY_PER_REQUEST}`,
  GET_FILTERED_ARTICLES_BY_TITLE: (filter: string) =>
    `${BASE_API_URL}?title_contains=${filter}&_limit=${ARTICLES_QUANTITY_PER_REQUEST}`,
  GET_FILTERED_ARTICLES_BY_BODY: (
    filter: string,
    limit = ARTICLES_QUANTITY_PER_REQUEST
  ) => `${BASE_API_URL}?summary_contains=${filter}&_limit=${limit}`,
});

